@import '../../../../styles/variables.scss';
//.prodpage-colors{}
.prodpage-colors{
    padding: 0;
    display: flex;
    flex-direction: column;
    // margin-right: 20px;
    // margin-bottom: 20px;
    //.prodpage-colors__name{}
    &__name{
        display: flex;
        margin-right: 10px;
        margin-bottom: 10px;
        padding: 0;
        &>span:first-child{
            color: $dg2;
        }
    }
    //.prodpage-colors__items{}
    &__items{
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 5px;
    }
    //.prodpage-colors__item{}
    &__item{
        padding: 0;
        width: 25px;
        height: 25px;
        margin-right: 7px;
        //.prodpage-colors__item--disable{}
        &--disable{
            cursor: not-allowed;
            position: relative;
            z-index: 5;
            &::after{
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: #00000078;
                z-index: 4;
                top: 0;
                left: 0;
            }
        }
    }
    //.prodpage-colors__btn{}
    &__btn{
        padding: 0;
        pointer-events: all;
        box-shadow: 0 0 0 3px var(--gx-color-gray) inset;
        &:hover{
            cursor: pointer;
        }
        //.prodpage-colors__btn--active{}
        &--active{
            box-shadow: inset 2px 2px 3px #000, inset -2px -2px 3px #000;
            transform: scale(1.1);
            pointer-events: none;
            &:hover{
                cursor: not-allowed;
            }
        }
        //.prodpage-colors__btn--disabled{}
        &--disabled{
            pointer-events: none;
            background-color: #c3c3c3;
        }
    }
}